/**
 * Enum for all the supported PropertyPane field types.
 *
 * Names should be consistent with those in office-ui-fabric-react, be careful to get letter casing correct.
 */
export enum IPropertyPaneFieldType {
  /**
   * Custom field.
   */
  Custom = 1,

  /**
   * Checkbox field.
   */
  Checkbox = 2,

  /**
   * TextField field.
   */
  TextField = 3,

  /**
   * Toggle field.
   */
  Toggle = 5,

  /**
   * Dropdown field.
   */
  Dropdown = 6,

  /**
   * Label field.
   */
  Label = 7,

  /**
   * Slider field.
   */
  Slider = 8,

  /**
   * Heading field.
   */
  Heading = 9,

  /**
   * Choice Group field.
   */
  ChoiceGroup = 10,

  /**
   * Button field.
   */
  Button = 11,

  /**
   * Horizontal Rule field.
   */
  HorizontalRule = 12,

  /**
   * Link field.
   */
  Link = 13
}
